<!-- by  -->
<template>
  <fixed-layout>
    <template slot="header">
      <topbar>
        <template slot="before">
          <b class="iconfont icon-back" @click="$router.go(-1)"></b>
        </template>
        
        <template slot="after">
          <button class="btn" @click="handNext"></button>
        </template>
      </topbar>
    </template>
    <section class="m-write">
      <div id="editor"></div>
    </section>
    <section class="m-uploads">
      <template v-for="(row,$index) in myPreimg">
        <div class="item">
          <img class="fit-cover" :src="row.base64"/>
          <b class="iconfont icon-close" @click="handDeleteImage(row,$index)"></b>
        </div>
      </template>
      <div class="item">
        <file-image @load="handLoadImg"></file-image>
      </div>
    </section>
  </fixed-layout>
</template>
<script>
  import { mapState } from 'vuex'
  import FixedLayout from '@/components/fixed-layout'
  import Topbar from '@/components/topbar'
  import FileImage from '@/components/file-image'
  import Quill from 'quill'
  import 'quill/dist/quill.bubble.css'

  let quill = null
  export default {
    name: '',
    components: {
      FixedLayout,
      Topbar,
      FileImage
    },
    props: {},
    data () {
      return {
        myPreimg: [],
      }
    },
    watch: {},
    computed: {
      ...mapState({
        tagId: state => state.write.post.tagId,
        clubId: state => state.write.post.clubId,
        content: state => state.write.post.content,
        preimg: state => state.write.post.preimg,
      })
    },
    created () {
      this.init()
    },
    mounted () {
      quill = new Quill('#editor', {
        modules: {
          toolbar: [
            [{header: [1, 2, false]}],
            ['bold', 'italic', 'underline'],
            ['image']
          ]
        },
        formats: {
          size: '120px',
          bold: true,
        },
        placeholder: '',
        theme: 'bubble'
      })
    },
    methods: {
      init () {
      },
      initData () {
      },
      handNext () {
        this.$store.commit(this.$actionType.set_post_data, {
          content: quill.getText(),
          preimg: this.myPreimg
        })
        let params = {
          clubId: this.clubId,
          tagId: this.tagId,
          cont: this.content,
          preimg: this.myPreimg.join('#')
        }
        this.$http.post(this.$port.POST_DISCUSS, this.$qs.stringify(params)).then(res => {
          alert(res.msg)
          if (res.code === 200) {
            this.$router.push({
              path: '/'
            })
          }
        })
      },
      uploadImg (file) {
        return new Promise((resolve, reject) => {
          let fd = new FormData()
          fd.append('imgType', 1)
          fd.append('upload', file)
          this.$http.post(this.$port.UPLOAD_IMG, fd).then(res => {
            resolve(res)
          })
        })
      },
      handLoadImg (e, file) {
        let img = {
          base64: e.target.result,
          file: file
        }
        this.myPreimg.push(img)
        this.uploadImg(file).then(res => {
          if (res.code === 200) {
            img.url = res.data
          } else {
            alert(res.msg)
          }
        })
      },
      handDeleteImage (row, index) {
        this.myPreimg.splice(index, 1)
      }
    }
  }
</script>
<style lang="scss">

</style>
<style lang="scss" scoped>
  .fixed-layout {
    background-color: #fff;
  }

  #editor {
    font-size: 16px;
  }

  .m-uploads {
    display: flex;
    flex-wrap: wrap;
    .item {
      height: 100px;
      margin-left: 10px;
      margin-bottom: 10px;
      flex: 0 0 100px;
      border: solid #e5e5e5 1px;
      position: relative;
      b {
        background-color: rgba(0, 0, 0, 0.5);
        text-align: center;
        font-weight: 400;
        font-size: 8px;
        color: #fff;
        width: 20px;
        height: 20px;
        position: absolute;
        right: 0;
        top: 0;
      }
      &:last-child {
        border-width: 0;
      }
    }
  }

  .m-write {
    margin-bottom: 10px;
  }
</style>



